import { createWebHashHistory, createRouter } from "vue-router"; //여기에 기본값인 createWebHistory를 쓰면 서버가 라우팅을 뺏어가 404오류가 뜰 수 있다.
import List from './components/List.vue'; //List.vue를 만들면 여기서 임포트
import Home from './components/Home.vue'; //Home.vue
import Detail from './components/Detail.vue'; 
import None from './components/None.vue'; 
import Author from './components/Author.vue'; 
import Comment from './components/Comment.vue';



const routes = [
  {
    path: "/detail/:id", //path를 지정하고 detail 뒤에 id는 아무문자라는 뜻 아무거나 써도 Detail.vue components로 연결, id는 다른 단어로 써도 무방
    //파라미터 id자리에 정규표현식을 쓸 수 도 있다. id(\\d+), id* 등등 뷰라우터 공식홈페이지에 있음
    component: Detail, //여기서 import한 이름 쓰기 ->main.js로
    children: [{
        path: "author",  //children은 '/'를 쓰지 않는다.
        component: Author,
        },
        {
            path: "comment", 
            component: Comment,
        }
    ],
  },
  {
    path: "/list", //path를 지정하고
    component: List, //여기서 import한 이름 쓰기 ->main.js로
    //children외에 components:{List : List, Component: Component}이런식으로 여러개의 component를 넣을 수 있고, 그 배치는 여러개의 <router-view>태그로한다.
  },
  {
    path: "/",
    // , redirect:{name: 'homepage'}이런식으로 움직일 수도 있다.
    component: Home, //여기서 import한 이름 쓰기 ->main.js로
  },
  {
    path: "/:anything(.*)", //path를 지정하고 anything은 가장 마지막이어야 하므로 순서가 중요하다. (.*)은 모든 경우를 뜻함
    component: None, //여기서 import한 이름 쓰기 ->main.js로
  }
];

const router = createRouter({
  history: createWebHashHistory(),  //여기에 기본값인 createWebHistory를 쓰면 서버가 라우팅을 뺏어가 404오류가 뜰 수 있다.
  routes,
});

export default router; 